
.header
{
  position: fixed;
  top: 0;
  left: 0;
  //left: $body-padding-desktop;
  margin: 0;
  padding-left: 1em;
  padding-right: 1em;

  width: 100%;
  //width: calc(100% - #{$body-padding-desktop + $body-padding-desktop});
  height: 64px;
  line-height: 64px;

  -moz-box-sizing: border-box;
  box-sizing: border-box;

  background: $cod-gray;
  font-size: 16px;
  font-weight: bold;
  color: #fff;


  //@include tablet {
  //  left: $body-padding-tablet;
  //  width: calc(100% - #{$body-padding-tablet + $body-padding-tablet});
  //}
  //
  //@include smartphone {
  //  left: 0;
  //  width: 100%;
  //}


  @include smartphone-landscape {
    height: 40px;
    line-height: 40px;
  }

}


#title {
  display: inline-block;
  margin-left: 1em;
}


#presetTitle {
  display: inline-block;
  float: right;
  font-size: 0.8em;
  font-weight: 300;
  letter-spacing: 4px;
  color: #fff;

  @include smartphone {
    font-size: 0.7em;
    letter-spacing: 2px;
  }
}

#presetTitle::after {
  float: none;
  clear: both;
}




#hamburgerBtn {
  position: relative;
  left: 0;
  /*bottom: 8px;*/
  display: inline-block;
  height: 40px;
  line-height: 40px;
  outline: none;
}

.hamburger-box {
  display: inline-block;
  vertical-align: middle;
  width: 48px !important;

  @include smartphone {
    width: 32px !important;
  }

  @include smartphone-landscape {
    width: 32px !important;
  }
}

.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
  width: 100% !important;
}

.hamburger {
  padding: 0 !important;
}






